<template>
    <div class="regimentList">
        <div class="header">
            <span @click="back" class="back">
                <img src="../../assets/img/back.png" class="releaseIcon">
            </span>
            <span class="usersTitle">哼哈团</span>
            <span class="back"></span>
            <span class="about">我的团</span>
        </div>
        <div class="searchBar">
          <input placeholder="请输入您想搜索的哼哈团">
          <img class="searchImg" src="../../assets/img/searchIcon.png">
        </div>
        <ul class="regiments">
          <li class="regiment" v-for="(regiment, index) in regiments" :key="index">
            <div class="information">
              <div class="inforLeft">
                <span class="headPic">
                  <img :src="regiment.headPic">
                  <div class="level">
                    <img :src="regiment.level === 1 ? lvl1 : (regiment.level === 2 ? lvl2 : (regiment.level === 3 ? lvl3 : (regiment.level === 4 ? lvl4 : lvl5)))">
                  </div>
                </span>
              </div>
              <div class="inforRight">
                <span class="details">
                  <span class="regimentName">
                    <span>{{ regiment.name }}</span>
                    <img class="chargeicon" src="../../assets/img/charge.png" v-if="regiment.charge">
                  </span>
                  <span class="memberCounts">共{{ regiment.member }}人</span>
                  <div class="introduce introduceAll" ref="introduce" @click="hide(index)">团队简介:{{ regiment.introduce }}</div>
                </span>
              </div>
            </div>
            <span class="more" @click="showAll(index)" v-if="!regiment.introduceAll">...更多详情</span>
          </li>
        </ul>
    </div>
</template>

<script>
export default {
  name: 'regimentList',
  data () {
    return {
      lvl1: require('../../assets/img/v1.png'),
      lvl2: require('../../assets/img/v2.png'),
      lvl3: require('../../assets/img/v3.png'),
      lvl4: require('../../assets/img/v4.png'),
      lvl5: require('../../assets/img/v5.png'),
      regiments: [{
        headPic: require('../../assets/img/headTest1.png'), name: '浦东太极一团1区', member: 50, introduce: '1111111111111111111111111111111111111111111111111111111111', level: 5, charge: true
      }, {
        headPic: require('../../assets/img/headTest1.png'), name: '浦东太极一团1区', member: 50, introduce: '1111111111111111111111111111111111111111111111111111111111', level: 3, charge: false
      }]
    }
  },
  mounted () {
    for (let i = 0; i < this.regiments.length; i++) {
      this.$set(this.regiments[i], 'introduceAll', false)
    }
  },
  methods: {
    back () {
      this.$router.go(-1)
    },
    showAll (index) {
      if (this.$refs.introduce[index].classList.contains('introduceAll')) {
        this.regiments[index].introduceAll = true
        this.$refs.introduce[index].classList.remove('introduceAll')
      }
    },
    hide (index) {
      if (!this.$refs.introduce[index].classList.contains('introduceAll')) {
        this.regiments[index].introduceAll = false
        this.$refs.introduce[index].classList.add('introduceAll')
      }
    }
  }
}
</script>

<style scoped>
  * {
    margin: 0;
    padding: 0;
    color: #333333;
    font-size: 0.48rem;
  }

  .header {
    width: 100%;
    height: 1.5rem;
    display: flex;
    justify-content: space-between;
    position: fixed;
    top: 0;
    left: 0;
    background: #fff;
    z-index: 99;
  }

  .header span {
    display: flex;
    justify-content: center;
    padding: 0.5rem;
    line-height: 0.5rem;
  }

  .back {
    height: 0.5rem;
    width: 0.266667rem;
  }

  img {
    width: 100%;
  }

  .about {
    position: absolute;
    right: 0;
    font-size: 0.42667rem;
  }

  .searchBar {
    width: 80%;
    margin: 0 auto;
    margin-top: 1.8rem;
    position: relative;
  }

  .searchBar input {
    width: calc(100% - 1rem);
    height: 1rem;
    padding: 0 0.5rem;
    border-radius: 0.5rem;
    outline: none;
    border: 1px solid #e6e6e6;
    font-size: 0.42667rem;
  }

  .searchImg {
    width: 0.5rem;
    height: 0.5rem;
    position: absolute;
    right: 0.25rem;
    top: calc(50% - 0.25rem);
  }

  .regiments {
    width: calc(100% - 0.8rem);
    margin: 0 auto;
    list-style: none;
  }

  .regiment {
    margin-top: 0.5rem;
    border-bottom: 1px solid #e6e6e6;
    padding-bottom: 0.2rem;
    position: relative;
  }

  .information {
    display: flex;
    position: relative;
  }

  .headPic {
    width: 1.6rem;
    height: 1.6rem;
    display: inline-block;
    border-radius: 50%;
    position: relative;
  }

  .headPic img {
    width: 100%;
    height: 100%;
    border-radius: 0.8rem;
  }

  .level {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    right: -0.1rem;
    bottom: -0.1rem;
    color: #fffcfc;
    width: 0.6rem;
    height: 0.6rem;
    border-radius: 50%;
    border: 2px solid #fffcfc;
    background: #ffd200;
  }

  .inforRight {
    margin-left: 0.48rem;
    margin-top: 0.3rem;
    position: relative;
  }

  .chargeicon {
    width: 0.6rem;
    height: 0.6rem;
  }

  .details span {
    display: block;
    text-align: start;
  }

  .regimentName {
    margin-bottom: 0.15rem;
    font-size: 0.42667rem;
    display: flex!important;
    align-items: center;
    justify-content: space-between;
  }

  .memberCounts,
  .introduce {
    font-size: 0.37333rem;
    color: #666666;
    text-align: start;
    word-break: break-all;
  }

  .introduceAll {
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
  }

  .more {
    font-size: 0.37333rem;
    color: #ffd200;
    position: absolute;
    right: 0;
    bottom: 0.2rem;
    background: #ffffff;
    padding: 0 0.2rem;
  }
</style>



